"use strict";

import help, { ModalFormRow } from './util/genericHelpers';
import randomColor from 'randomcolor';
import GraphState, { AddNodeI, GraphStateHistory } from './graphState';
import GraphImmut from "./classes/GraphImmut/GraphImmut";
import { NodeImmutPlain } from "./classes/GraphImmut/NodeImmut";
import { EdgeImmutPlain } from "./classes/GraphImmut/EdgeImmut";
import { GraphPlain } from "./util/predefinedGraphs";
import { Network, Node as VisNode, Edge } from "vis-network";
import { DataSet } from "vis-data";
import * as languages from "./languages";


export interface MainI {
    graphState: typeof GraphState;
    container: HTMLElement;
    visWeightEdgeEdit: (data: VisEditEdgeInternal, callback: Function) => void;
    visOptions: {
        interaction: { hover: boolean };
        manipulation: {
            addNode: (data: AddNodeI, callback: Function) => void;
            editNode: (data: AddNodeI, callback: Function) => void;
            addEdge: (data: VisEdgeInternal, callback?: Function) => void;
            editEdge: (data: VisEdgeInternal, callback: Function) => void;
            deleteEdge: (data: { edges: string[] }, callback?: Function) => void;
            deleteNode: (data: { nodes: string[] }, callback: Function) => void
        },
        locale?: string,
        locales?: any,
    };
    cancelEdit: (callback: Function) => void;
    saveData: (data: any, callback: Function, operation: string, label: string, color: string) => Promise<void>;
    nodeLabelIDValidator: (v: string) => (boolean | string);
    applyColors: () => Promise<void>;
    setData: (data: GraphPlain, recalcProps?: boolean, graphChanged?: boolean, rearrangeGraph?: boolean) => void;
    saveState: () => void;
    getStateForSaving: () => GraphStateHistory;
    undo: () => void;
    redo: () => void;
    applyState: (undo?: boolean, newState?: any) => void;
    saveStateLocalStorage: () => void;
    shuffleNetworkLayout: () => void;
    randomizeNetworkLayoutSeed: (network: VisNetworkInternals) => void;
    addNetworkListeners: (network: Network) => void;
    resetSevenBridgeWalk: () => void;
    updateRamseyStatus: () => void;
}

interface VisNetworkEvent {
    edges: DataSet<Edge>;
    nodes: DataSet<VisNode>;
}

interface VisNetworkInternals extends Network {
    layoutEngine: {
        randomSeed: number,
        initialRandomSeed: number
    }
}

interface VisEditEdgeInternal {
    from: { id: string | number };
    to: { id: string | number };
    label?: string
}

interface VisEdgeInternal {
    from: string | number;
    to: string | number;
    id: string;
    label?: string
}

const customColorPallete = {
    "Default": "DEFAULT",
    "Red": "red",
    "Maroon": "maroon",
    "Yellow": "yellow",
    "Olive": "olive",
    "Lime": "lime",
    "Green": "green",
    "Aqua": "aqua",
    "Teal": "teal",
    "Blue": "blue",
    "Navy": "navy",
    "Fuchsia": "fuchsia",
    "Purple": "purple",
};

const fourColorPalette = {
    "Default": "DEFAULT",
    "Red": "#e74c3c",
    "Green": "#27ae60",
    "Blue": "#3498db",
    "Yellow": "#f1c40f"
};

const getActivePalette = () => {
    if (window.settings.getOption("fourColorMode")) {
        return fourColorPalette;
    }
    return customColorPallete;
};

const getEdgeColor = (from: string | number, to: string | number, oldWeight: number | null, edgeId?: string | number) => {
    const edgeFrom = parseInt(from as string, 10);
    const edgeTo = parseInt(to as string, 10);
    const edges = GraphState.graph.getAllEdges() as EdgeImmutPlain[];
    const match = edges.find(edge => {
        if (typeof edgeId !== "undefined" && edgeId !== null) {
            if ("id" in edge && `${(edge as any).id}` === `${edgeId}`) {
                return true;
            }
            return false;
        }
        const directedMatch = edge.from === edgeFrom && edge.to === edgeTo;
        const undirectedMatch = !GraphState.graph.isDirected() && edge.from === edgeTo && edge.to === edgeFrom;
        const weightMatch = oldWeight === null || edge.weight === oldWeight;
        return (directedMatch || undirectedMatch) && weightMatch;
    });
    if (!match || !("color" in match)) {
        return "DEFAULT";
    }
    return (match as any).color;
};

let sevenBridgeCurrentNodes: Array<number | string> = [];

const updateSevenBridgeStatus = (message: string | null = null) => {
    const body = document.getElementById("seven-bridge-status-body");
    if (!body) {
        return;
    }
    if (!window.settings.getOption("sevenBridgeMode")) {
        body.innerHTML = "";
        return;
    }
    let currentText = languages.current.SevenBridgesNoCurrentNode;
    if (sevenBridgeCurrentNodes.length > 0) {
        const joined = sevenBridgeCurrentNodes
            .map((id) => help.htmlEncode(GraphState.nodeIDToLabel(parseInt(id as string, 10))))
            .join(` ${languages.current.Or} `);
        currentText = help.stringReplacement(languages.current.SevenBridgesCurrentNodes, joined);
    }
    let html = `<p>${currentText}</p>`;
    if (message) {
        html += `<p class="text-danger">${message}</p>`;
    }
    body.innerHTML = html;
    applySevenBridgeNodeHighlight();
};

const applySevenBridgeNodeHighlight = () => {
    if (!window.settings.getOption("sevenBridgeMode")) {
        return;
    }
    if (!window.settings.getOption("customColors")) {
        window.settings.changeOption("customColors", true);
    }
    const nodes = GraphState.graph.getAllNodes() as NodeImmutPlain[];
    const edges = GraphState.graph.getAllEdges() as EdgeImmutPlain[];
    nodes.forEach((node) => {
        const isCurrent = sevenBridgeCurrentNodes.includes(node.id);
        node.color = isCurrent ? "red" : null;
    });
    edges.forEach((edge, index) => {
        if (typeof (edge as any).id === "undefined") {
            (edge as any).id = index;
        }
        if ("color" in edge && edge.color === "red") {
            return;
        }
        edge.color = "#848484";
    });
    GraphState.graph = new GraphImmut(nodes, edges, GraphState.graph.isDirected(), GraphState.graph.isWeighted());
    if (window.network && (window.network as any).body?.data) {
        const nodeUpdates = nodes.map((node) => ({
            id: node.id,
            color: node.color ? { background: node.color } : null
        }));
        const edgeUpdates = edges.map((edge) => ({
            id: (edge as any).id,
            color: edge.color ? { color: edge.color } : undefined
        }));
        (window.network as any).body.data.nodes.update(nodeUpdates);
        (window.network as any).body.data.edges.update(edgeUpdates);
    }
};

const updateRamseyStatus = () => {
    if (!window.settings.getOption("ramseyNumberMode")) {
        return;
    }
    const statusBody = document.getElementById("ramsey-number-status-body");
    if (!statusBody) {
        return;
    }
    const sInput = document.getElementById("ramsey-number-s") as HTMLInputElement | null;
    const tInput = document.getElementById("ramsey-number-t") as HTMLInputElement | null;
    const sValue = sInput ? parseInt(sInput.value, 10) : 0;
    const tValue = tInput ? parseInt(tInput.value, 10) : 0;
    const nodes = GraphState.graph.getAllNodes() as NodeImmutPlain[];
    const edges = GraphState.graph.getAllEdges() as EdgeImmutPlain[];

    if (!Number.isFinite(sValue) || !Number.isFinite(tValue) || sValue <= 0 || tValue <= 0) {
        statusBody.innerHTML = "<p>Please enter positive integers for s and t.</p>";
        return;
    }

    if (sValue > nodes.length || tValue > nodes.length) {
        statusBody.innerHTML = "<p>s or t is larger than the number of vertices.</p>";
        return;
    }

    const keyFor = (from: number, to: number) => {
        if (GraphState.graph.isDirected()) {
            return `${from}->${to}`;
        }
        return from < to ? `${from}-${to}` : `${to}-${from}`;
    };

    const edgeColorMap = new Map<string, string>();
    edges.forEach((edge) => {
        const color = (edge as any).color as string | undefined;
        const normalized = color === "red" ? "red" : "default";
        edgeColorMap.set(keyFor(edge.from, edge.to), normalized);
    });

    const isRedEdge = (from: number, to: number) => edgeColorMap.get(keyFor(from, to)) === "red";
    const isDefaultEdge = (from: number, to: number) => edgeColorMap.get(keyFor(from, to)) === "default";

    const nodeIds = nodes.map((n) => n.id as number);

    const findSubset = (size: number, isValid: (subset: number[]) => boolean): number[] | null => {
        const result: number[] = [];
        const dfs = (start: number, left: number): boolean => {
            if (left === 0) {
                return isValid(result);
            }
            for (let i = start; i <= nodeIds.length - left; i++) {
                result.push(nodeIds[i]);
                if (dfs(i + 1, left - 1)) {
                    return true;
                }
                result.pop();
            }
            return false;
        };
        return dfs(0, size) ? [...result] : null;
    };

    const redClique = findSubset(sValue, (subset) => {
        for (let i = 0; i < subset.length; i++) {
            for (let j = i + 1; j < subset.length; j++) {
                if (!isRedEdge(subset[i], subset[j])) {
                    return false;
                }
            }
        }
        return true;
    });

    const defaultClique = findSubset(tValue, (subset) => {
        for (let i = 0; i < subset.length; i++) {
            for (let j = i + 1; j < subset.length; j++) {
                if (!isDefaultEdge(subset[i], subset[j])) {
                    return false;
                }
            }
        }
        return true;
    });

    const redText = redClique ? redClique.join(", ") : "None";
    const defaultText = defaultClique ? defaultClique.join(", ") : "None";
    statusBody.innerHTML = `<p>Red clique (size s="${sValue}"): ${redText}</p><p>Blue clique (size t="${tValue}"): ${defaultText}</p>`;
};

const hasFourColorConflict = (nodeId: number, color: string) => {
    if (color === "DEFAULT") {
        return false;
    }
    const adjacency = GraphState.graph.getNodeAdjacency(nodeId);
    return adjacency.some(adjacentId => {
        const adjacent = GraphState.graph.getNode(adjacentId) as NodeImmutPlain | boolean;
        if (!adjacent || typeof adjacent === "boolean") {
            return false;
        }
        return (adjacent as any).color === color;
    });
};

const self: MainI = {
    graphState: GraphState,
    container: document.getElementById('network')!,
    // Function used to overwrite the edge edit functionality when weights are active
    visWeightEdgeEdit: (data: VisEditEdgeInternal, callback) => {
        const oldWeight = parseFloat(data.label!);
        const options: ModalFormRow[] = [
            {
                type: "numeric",
                label: languages.current.WeightCapacity,
                initialValue: oldWeight
            }
        ];
        const palette = getActivePalette();
        options.push({
            type: "select",
            label: languages.current.Color,
            optionText: Object.keys(palette),
            optionValues: Object.values(palette),
            initialValue: getEdgeColor(data.from.id, data.to.id, oldWeight)
        });

        help.showFormModal(($modal, vals) => {
            callback(null);
            $modal.modal("hide");
            const value = parseFloat(vals[0]);
            const selectedColor = vals[1] as string;
            const color = selectedColor === "DEFAULT" ? null : selectedColor;
            if (color && !window.settings.getOption("customColors")) {
                window.settings.changeOption("customColors", true);
            }
            GraphState.editEdge(data.from.id, data.to.id, value, oldWeight, color, true);
        }, languages.current.EditEdge, languages.current.Save, languages.current.Cancel, [
            ...options
        ]);
    },
    visOptions: {
        interaction: { hover: true },
        manipulation: {
            addNode: async (data, callback) => {
                const customColors = window.settings.getOption("customColors");
                const options: ModalFormRow[] = [
                    {
                        type: "html",
                        initialValue: `<p>${help.stringReplacement(languages.current.NodeId, await GraphState.getProperty("vertices"))}</p>`
                    },
                    { type: "text", label: languages.current.LabelLabel, initialValue: await GraphState.getProperty("vertices") },
                ];
                if (customColors) {
                    const palette = getActivePalette();
                    options.push({ type: "select", label: languages.current.Color, optionText: Object.keys(palette), optionValues: Object.values(palette) });
                }
                const $popup = help.makeFormModal(languages.current.AddNode, languages.current.Save, languages.current.Cancel, options);

                $popup.on("click", ".btn-success", () => {
                    $popup.modal("hide");
                    self.saveData(data, callback, "add", $popup.find("input").first().val() as string, $popup.find("select").first().val() as string);
                }).on("click", ".btn-cancel", () => {
                    $popup.modal("hide");
                    self.cancelEdit(callback);
                }).on("hidden.bs.modal", () => {
                    $popup.remove();
                    self.cancelEdit(callback);
                }).modal("show");
            },
            editNode: (data, callback) => {
                const customColors = window.settings.getOption("customColors");
                const initialColor = Object.getOwnPropertyNames(data.color).includes("background") ? (data.color as any).background : "DEFAULT";

                const options: ModalFormRow[] = [
                    {
                        type: "html",
                        initialValue: `<p>${help.stringReplacement(languages.current.NodeId, data.id + "")}</p>`
                    },
                    { type: "text", label: languages.current.LabelLabel, initialValue: data.label },
                ];
                if (customColors) {
                    const palette = getActivePalette();
                    options.push({ type: "select", label: languages.current.Color, optionText: Object.keys(palette), optionValues: Object.values(palette), initialValue: initialColor });
                }
                const $popup = help.makeFormModal(languages.current.EditNode, languages.current.Save, languages.current.Cancel, options);

                $popup.on("click", ".btn-success", () => {
                    $popup.modal("hide");
                    self.saveData(data, callback, "editNode", $popup.find("input").first().val() as string, $popup.find("select").first().val() as string);
                }).on("click", ".btn-cancel", () => {
                    $popup.modal("hide");
                    self.cancelEdit(callback);
                }).on("hidden.bs.modal", () => {
                    $popup.remove();
                    self.cancelEdit(callback);
                }).modal("show");
            },
            addEdge: (data, callback) => {
                const apply = () => {
                    if (typeof callback === "function") {
                        callback(null);
                    }
                    GraphState.addEdge(data.from, data.to);
                };
                if (data.from === data.to) {
                    if (confirm(languages.current.ConnectNodeToItselfConfirmation)) {
                        apply();
                    }
                    return;
                }

                apply();
            },
            editEdge: (data, callback) => {
                const palette = getActivePalette();
                const existingEdge = (window.network as any).body.data.edges.get(data.id);
                const initialColor = existingEdge
                    ? getEdgeColor(existingEdge.from, existingEdge.to, null)
                    : getEdgeColor(data.from, data.to, null);
                const options: ModalFormRow[] = [
                    {
                        type: "select",
                        label: languages.current.Color,
                        optionText: Object.keys(palette),
                        optionValues: Object.values(palette),
                        initialValue: initialColor
                    }
                ];

                callback(null);
                self.visOptions.manipulation.deleteEdge({ edges: [data.id] });
                self.visOptions.manipulation.addEdge(data);

                const preservedColor = initialColor === "DEFAULT" ? null : initialColor;
                if (preservedColor) {
                    GraphState.editEdge(data.from, data.to, null, null, preservedColor, true);
                }

                const $popup = help.makeFormModal(languages.current.EditEdge, languages.current.Save, languages.current.Cancel, options);
                $popup.on("click", ".btn-success", () => {
                    $popup.modal("hide");
                    const selectedColor = $popup.find("select").first().val() as string;
                    const color = selectedColor === "DEFAULT" ? null : selectedColor;
                    if (color && !window.settings.getOption("customColors")) {
                        window.settings.changeOption("customColors", true);
                    }
                    if (color) {
                        GraphState.editEdge(data.from, data.to, null, null, color, true);
                    }
                }).on("click", ".btn-cancel", () => {
                    $popup.modal("hide");
                }).on("hidden.bs.modal", () => {
                    $popup.remove();
                }).modal("show");
            },
            deleteEdge: (data, callback) => {
                if (typeof callback === "function") {
                    callback(null);
                }
                data.edges.forEach((v: any) => {
                    let weight = null;
                    const edge = (window.network as any).body.data.edges.get(v);
                    const weightFromLabel = edge.label;
                    if (typeof weightFromLabel !== "undefined") {
                        weight = parseFloat(weightFromLabel);
                    }

                    GraphState.deleteEdge(edge.from, edge.to, weight);
                });
            },
            deleteNode: (data, callback) => {
                callback(null);
                data.nodes.forEach((v: string) => {
                    GraphState.deleteNode(v);
                });
            },
        },
    },

    cancelEdit: (callback) => {
        if (typeof callback === "function") {
            callback(null);
        }
    },

    saveData: async (data, callback, operation, label, color) => {
        callback(null);

        data.label = label;
        data.color = color;
        if (color === "DEFAULT") {
            data.color = undefined;
        }
        if (operation === "add") {
            GraphState.addNode(data);
        }
        else if (operation === "editNode") {
            if (window.settings.getOption("fourColorMode") && data.color) {
                const conflict = hasFourColorConflict(parseInt(data.id, 10), data.color);
                if (conflict) {
                    help.showSimpleModal(
                        languages.current.FourColorConflictTitle,
                        `<p>${languages.current.FourColorConflictBody}</p>`
                    );
                    return;
                }
            }
            GraphState.editNode(data.id, data.label, data.color);
        }
    },

    nodeLabelIDValidator: (v) => {
        if (GraphState.nodeLabelToID(v) > -1) {
            return true;
        }
        return languages.current.InvalidLabelOrId;
    },

    applyColors: async () => {
        if (window.settings.getOption("direction")) {
            return;
        }
        const graphColors = await GraphState.getProperty("graphColoring", true);
        const chromaticNumber = await GraphState.getProperty("Chromatic Number", true);

        const colors = randomColor({ count: chromaticNumber, luminosity: "light" });
        let G = GraphState.graph;
        (G.getAllNodes() as NodeImmutPlain[]).forEach((v) => {
            G = G.editNode(v.id, { color: colors[graphColors[v.id]] });
        });
        self.setData(GraphState.getGraphData(G), false, false);
    },

    setData: (data, recalcProps = false, graphChanged = true, rearrangeGraph = false) => {
        // Store existing positions in the data if we're supposed to keep the layout
        if (rearrangeGraph) {
            data.nodes.forEach((v) => {
                delete v.x;
                delete v.y;
            });
        }

        if (graphChanged) {
            self.saveState();
        }

        if ("directed" in data && typeof data.directed !== "undefined") {
            window.settings.changeOption("direction", data.directed);
        }
        if ("weighted" in data && typeof data.weighted !== "undefined") {
            window.settings.changeOption("weights", data.weighted);
        }
        const directional = window.settings.getOption("direction") as boolean;
        const weighted = window.settings.getOption("weights") as boolean;

        const g = new GraphImmut(data.nodes, data.edges, directional, weighted);
        GraphState.graph = g;

        // Set a new random seed so that the layout will be different
        self.randomizeNetworkLayoutSeed(window.network as unknown as VisNetworkInternals);
        window.network.setData(GraphState.getGraphAsDataSet(g));
        GraphState.graph = GraphState.setLocations(window.network.getPositions());

        window.network.disableEditMode();
        window.network.enableEditMode();

        if (graphChanged) {
            window.ui.stopLoadingAnimation();
            window.ui.isRunning = {};
            window.ui.terminateAllWebWorkers();
            window.ui.printGraphAlgorithms();
            help.printout("");
            GraphState.setUpToDate();
            GraphState.makeAndPrintProperties(recalcProps);
        }

        self.saveStateLocalStorage();
    },

    saveState: () => {
        if (GraphState.graph === null) {
            return;
        }

        if (GraphState.backHistory.length >= GraphState.maxHistory) {
            GraphState.backHistory.shift();
        }

        GraphState.backHistory.push(self.getStateForSaving());
        GraphState.forwardHistory = [];
        document.getElementsByClassName("icon-undo")!.item(0)!.parentElement!.parentElement!.classList.add("active");
    },

    getStateForSaving: () => {
        const state: any = {};
        Object.keys(GraphState).forEach((k: string) => {
            const v: any = (GraphState as any)[k];
            if (typeof v !== "function") {
                if (typeof v !== "object") {
                    state[k] = v;
                }
                else {
                    if (k === "graph" && v !== null) {
                        state[k] = v;
                    }
                    if (!k.toLowerCase().includes("history")) {
                        state[k] = help.deepCopy(true, Array.isArray(v) ? [] : {}, v);
                    }
                }
            }
        });

        return state as GraphStateHistory;
    },

    undo: () => {
        if (GraphState.backHistory.length > 0) {
            self.applyState(true);
        }
    },

    redo: () => {
        if (GraphState.forwardHistory.length > 0) {
            self.applyState(false);
        }
    },

    applyState: (undo = true, newState: null | GraphStateHistory = null) => {
        const firstLoad = newState !== null;
        const currentState = self.getStateForSaving();

        if (!firstLoad) {
            if (undo) {
                newState = GraphState.backHistory.pop()!;
            }
            else {
                newState = GraphState.forwardHistory.pop()!;
            }
        }

        //@ts-ignore Ignore accessing private props. I do this because saving the state lost the type of the data
        newState.graph = new GraphImmut(newState.graph.nodes, newState.graph.edges, newState.graph.directed, newState.graph.weighted);

        window.settings.changeOption("direction", newState!.graph.isDirected());
        window.settings.changeOption("weights", newState!.graph.isWeighted());

        GraphState.graph = newState!.graph;

        window.network.setData(GraphState.getGraphAsDataSet(GraphState.graph));
        window.network.disableEditMode();
        window.network.enableEditMode();

        window.ui.printGraphAlgorithms();
        help.printout("");

        Object.keys(newState!).forEach((k: string) => {
            const v = newState![k];
            if (typeof v !== "object") {
                (GraphState as any)[k] = v;
            }
            else if (!k.toLowerCase().includes("history") && k.toLowerCase() !== "graph") {
                if (k.toLowerCase() === "uptodate") {
                    Object.keys((GraphState as any)[k]).forEach((oldKey) => {
                        (GraphState as any)[k][oldKey].upToDate = (v as any)[oldKey].upToDate;
                    });
                }
                else {
                    (GraphState as any)[k] = help.deepCopy(true, (GraphState as any)[k], v);
                }
            }
        });

        GraphState.makeAndPrintProperties().then(() => {
            if (undo && !firstLoad) {
                document.getElementsByClassName("icon-redo")!.item(0)!.parentElement!.parentElement!.classList.add("active");
                if (GraphState.backHistory.length === 0) {
                    document.getElementsByClassName("icon-undo")!.item(0)!.parentElement!.parentElement!.classList.remove("active");
                }
                GraphState.forwardHistory.push(currentState);
            }
            else if (!undo && !firstLoad) {
                document.getElementsByClassName("icon-undo")!.item(0)!.parentElement!.parentElement!.classList.add("active");
                if (GraphState.forwardHistory.length === 0) {
                    document.getElementsByClassName("icon-redo")!.item(0)!.parentElement!.parentElement!.classList.remove("active");
                }
                GraphState.backHistory.push(currentState);
            }

            self.saveStateLocalStorage();
        });
    },

    saveStateLocalStorage: () => {
        if (window.settings.checkForLocalStorage()) {
            localStorage.setItem("graphPlayground.lastState", JSON.stringify(self.getStateForSaving()));
        }
    },

    shuffleNetworkLayout: () => {
        self.setData({
            nodes: GraphState.graph.getAllNodes() as NodeImmutPlain[],
            edges: GraphState.graph.getAllEdges() as EdgeImmutPlain[]
        }, false, false, true);
    },

    randomizeNetworkLayoutSeed: (network) => {
        const r = Math.round(Math.random() * 1000000);
        network.layoutEngine.randomSeed = r;
        network.layoutEngine.initialRandomSeed = r;
    },

    addNetworkListeners: (network) => {
        // Enable edit node/edge when double clicking
        network.on("doubleClick", (p: VisNetworkEvent) => {
            if (window.settings.getOption("weights") && "edges" in p && p.edges.length === 1) {
                network.editEdgeMode();
            }
            if ("nodes" in p && p.nodes.length === 1) {
                lastNetworkClickEvent = null;
                network.editNode();
            }
        });

        // Save locations of nodes after dragging
        network.on("dragEnd", () => {
            GraphState.graph = GraphState.setLocations(network.getPositions());
            self.saveStateLocalStorage(); // Save the new locations as part of the state
        });

        // Delete nodes/edges when hit "Delete"
        let lastNetworkClickEvent: Event | null = null;
        network.on('click', (event) => {
            lastNetworkClickEvent = event;
            if (window.settings.getOption("sevenBridgeMode")
                && "edges" in event
                && (event as any).edges.length === 1) {
                const edgeId = (event as any).edges[0];
                const edgeData = (network as any).body.data.edges.get(edgeId);
                if (edgeData) {
                    const from = edgeData.from;
                    const to = edgeData.to;
                    let oldWeight: number | null = null;
                    if (typeof edgeData.label !== "undefined") {
                        const parsedWeight = parseFloat(edgeData.label);
                        if (!Number.isNaN(parsedWeight)) {
                            oldWeight = parsedWeight;
                        }
                    }
                    const existingColor = getEdgeColor(from, to, oldWeight, edgeId);
                    if (existingColor === "red") {
                        updateSevenBridgeStatus(languages.current.SevenBridgesRepeatEdgeError);
                        return;
                    }
                    const fromIn = sevenBridgeCurrentNodes.includes(from);
                    const toIn = sevenBridgeCurrentNodes.includes(to);

                    if (sevenBridgeCurrentNodes.length > 0 && !fromIn && !toIn) {
                        updateSevenBridgeStatus(languages.current.SevenBridgesJumpError);
                        return;
                    }

                    if (sevenBridgeCurrentNodes.length === 0) {
                        sevenBridgeCurrentNodes = [from, to];
                    }
                    else if (fromIn && toIn) {
                        if (from === to) {
                            sevenBridgeCurrentNodes = [from];
                        }
                        else {
                            sevenBridgeCurrentNodes = [from, to];
                        }
                    }
                    else {
                        const nextNode = fromIn ? to : from;
                        sevenBridgeCurrentNodes = [nextNode];
                    }

                    if (!window.settings.getOption("customColors")) {
                        window.settings.changeOption("customColors", true);
                    }
                    GraphState.editEdgeById(edgeId, null, "red", true);
                    updateSevenBridgeStatus();
                }
            }
            if (window.settings.getOption("ramseyNumberMode")
                && "edges" in event
                && (event as any).edges.length === 1) {
                const edgeId = (event as any).edges[0];
                if (!window.settings.getOption("customColors")) {
                    window.settings.changeOption("customColors", true);
                }
                GraphState.editEdgeById(edgeId, null, "red", true);
                updateRamseyStatus();
            }
        });

        network.on("oncontext", (event: any) => {
            if (!window.settings.getOption("ramseyNumberMode")) {
                return;
            }
            const edgeId = (network as any).getEdgeAt(event.pointer.DOM);
            if (typeof edgeId !== "undefined" && edgeId !== null) {
                event.event.preventDefault();
                GraphState.editEdgeById(edgeId, null, null, true, true);
                updateRamseyStatus();
            }
        });

        // Delete key to delete node or edge
        document.addEventListener('keyup', (key) => {
            if (key.key === "Delete" && lastNetworkClickEvent !== null) {
                if (self.container.contains((lastNetworkClickEvent as any).event.target)) {
                    if (("edges" in lastNetworkClickEvent && (lastNetworkClickEvent as any).edges.length === 1)
                        || ("nodes" in lastNetworkClickEvent && (lastNetworkClickEvent as any).nodes.length === 1)) {
                        network.deleteSelected();
                    }
                }
            }
        });

        // Undo/Redo keyboard commands
        document.addEventListener("keydown", (e) => {
            if ((e.key.toLowerCase() === 'y' && e.ctrlKey) || (e.key.toLowerCase() === 'z' && e.ctrlKey && e.shiftKey)) {
                self.redo();
            }
            else if (e.key.toLowerCase() === 'z' && e.ctrlKey) {
                self.undo();
            }
        });

        // When clicking off of the network, remove the Delete functionality
        document.addEventListener("click", (e) => {
            if (self.container !== e.target && !self.container.contains(e.target as Node)) {
                lastNetworkClickEvent = null;
            }
        });

        updateSevenBridgeStatus();
        updateRamseyStatus();
    },

    resetSevenBridgeWalk: () => {
        sevenBridgeCurrentNodes = [];
        const cleared = GraphState.getGraphData(GraphState.graph, true, true);
        self.setData(cleared, false, false);
        updateSevenBridgeStatus();
    },
    updateRamseyStatus: () => {
        updateRamseyStatus();
    },

};

export default self;
